'use strict';

// tag::vars[]
const React = require('react');
const ReactDOM = require('react-dom');
import {
	Nav,
	NavItem,
	PageHeader
	} from '@sketchpixy/rubix';

import Datatablesjs from './Datatablesjs';
import client from './client';
//import './../../main.css';

class App extends React.Component {

	constructor(props) {
		super(props);
		
		this.state = {
			selectedKey: 1	
		};
	}
	
	componentWillMount() {
		
	}

	componentDidMount() {

	}
	
	handleSelect(selectedKey) {
		this.setState({
			selectedKey: selectedKey
		});
	}

	render() {
		const navInstance = (
			<div>
			  <Nav bsStyle="pills" className='nav-orange75' activeKey={this.state.selectedKey} onSelect={this.handleSelect.bind(this)}>
				<NavItem eventKey={1}></NavItem>
				<NavItem eventKey={2}></NavItem>
				<NavItem eventKey={3}></NavItem>
				<NavItem eventKey={4}></NavItem>
			  </Nav>
			  
			  <Datatablesjs selectedKey={this.state.selectedKey}/>
			 </div>
		);
		
		return navInstance;
	}
}



ReactDOM.render(
	<App />,
	document.getElementById('react')
)





